import React from "react";
import { Route, BrowserRouter as Router } from "react-router-dom";
import { Link } from "react-router-dom";
import "../styles.css";
import Slika from "../gnome.jpeg";

export default function Naslovna() {
  return (
    <div className="Naslovna">
      <Link to="/Druga" class="linkic">
        Ovo je naslovna stranica na njoj nema mnogo sadržaja <br />
        ,ali možete pritisnuti ovaj Link za drugu stranicu.
      </Link>

      <br />
      <br />
      <br />
      <img src={Slika} class="gnom" />
    </div>
  );
}
